export default{
	data () {
		return {
			provider: ''
		}
	},
	onShow() {
		
	},
	onLoad() {
		
	},
	methods:{
		fnGetProvide () {
			uni.getProvider({
				service: "push",
				success: (e) => {
					console.log("success", e);
					this.provider = e.provider;
					uni.showToast({
						title: JSON.stringify(this.provider),
						icon: 'none'
					})
				},
				fail: (e) => {
					console.log("获取推送通道失败", e);
				}
			});
		},
		fnCreate () {
			plus.push.createMessage( '我是一条测试消息', {
				payload: true
			},{
				appid: '_UNI_B73181C',
				cover: true,
				icon: '/static/images/hb.png',
				sound: 'system',
				title: '标题',
				subtitle: '副标题'
			});
		}
	}
}
